absolute;\n  right: 0;\n  top: 100%;\n  z-index: 100;\n  background: #fff;\n  width: 290px;\n  border-radius: 10px;\n  box-shadow: 0 10px 29px 0 $shadow;\n\n  &__heading {\n    padding: 22px 10px 22px 30px;\n    color: $primary;\n    font-size: 14px;\n    font-weight: 400;\n    line-height: 1;\n    position: relative;\n  }\n\n  &__close {\n    position: absolute;\n    width: 20px;\n    height: 20px;\n    right: 10px;\n    top: calc(50% - 10px);\n  }\n\n  &__close-ico {\n    width: 100%;\n    height: 100%;\n    fill: $red;\n  }\n\n  &__body {\n    border-top: 1px solid $borderColor;\n    border-bottom: 1px solid $borderColor;\n  }\n\n  &__product-list {\n    max-height: calc(100vh - 220px);\n    overflow: auto;\n  }\n\n  &__product-item {\n    padding: 20px 0 20px 30px;\n    border-bottom: 1px solid $borderColor;\n    display: flex;\n    align-items: flex-start;\n    position: relative;\n\n    &:last-child {\n      border-bottom: 0;\n    }\n  }\n\n  &__product-image {\n    width: 100%;\n    max-width: 70px;\n    position: relative;\n    margin-right: 15px;\n    border-radius: 5px;\n    overflow: hidden;\n\n    &::after {\n      content: \"\";\n      padding-top: 100%;\n      display: block;\n    }\n  }\n\n  &__product-thumb {\n    position: absolute;\n    left: 0;\n    top: 0;\n    width: 100%;\n    height: 100%;\n    max-width: none;\n    object-fit: cover;\n    object-position: center;\n  }\n\n  &__product-content {\n    width: 100%;\n    max-width: calc(100% - 85px);\n  }\n\n  &__product-name {\n    color: $primary;\n    font-size: 12px;\n    font-weight: 700;\n    margin-bottom: 5px;\n    text-decoration: underline;\n  }\n\n  &__product-code {\n    color: $primary;\n    font-size: 12px;\n    font-weight: 400;\n    margin-bottom: 5px;\n  }\n\n  &__product-price {\n    color: $primary;\n    font-size: 14px;\n    font-weight: 600;\n    margin-bottom: 5px;\n\n    &--strike {\n      color: $brown;\n      font-weight: 400;\n      text-decoration: line-through;\n    }\n