: $primary;\n    white-space: nowrap;\n    @media(max-width: 575px) {\n      font-size: 14px;\n    }\n  }\n\n  &__sorting-arrow {\n    position: absolute;\n    right: 15px;\n    top: calc(50% - 5px);\n    width: 10px;\n    height: 10px;\n    color: $blue;\n    transition: transform .3s ease;\n  }\n\n  &__sorting-dropdown {\n    padding: 20px 0;\n    background: $blue;\n    box-shadow: 7px 7px 30px $shadow;\n    border-radius: 10px;\n    margin-top: 20px;\n    width: 100%;\n    position: absolute;\n    top: 100%;\n    left: 0;\n    opacity: 0;\n    pointer-events: none;\n    transition: opacity 0.3s;\n    @media(max-width: 575px) {\n      padding: 15px 0;\n      border-radius: 15px;\n      margin-top: 10px;\n    }\n  }\n\n  &__sorting-item {\n    position: relative;\n    padding: 10px 30px 10px 30px;\n    color: #FFF;\n    opacity: .8;\n    font-weight: 500;\n    font-size: 14px;\n    transition: opacity 0.3s;\n\n    @media(max-width: 575px) {\n      padding: 8px 20px;\n    }\n\n    &:hover {\n      opacity: 1;\n    }\n  }\n\n  &__buttons {\n    display: flex;\n    align-items: center;\n    justify-content: flex-end;\n    flex-wrap: wrap;\n\n    @media(max-width: 991px) {\n      max-width: 100%;\n      width: 100%;\n      margin: 20px -5px 0 -5px;\n      justify-content: center;\n    }\n  }\n\n  &__btn {\n    color: #FFF;\n    font-weight: 600;\n    font-size: 15px;\n    padding: 0 20px;\n    height: 51px;\n    display: inline-flex;\n    align-items: center;\n    justify-content: center;\n    background: $blue;\n    margin-right: 15px;\n    border-radius: 7px;\n    transition: background .3s ease;\n\n    @media(max-width: 1199px) {\n      padding: 0 15px;\n      height: 45px;\n      font-size: 14px;\n    }\n\n    @media(max-width: 991px) {\n      margin: 0 5px;\n    }\n\n    @media(max-width: 767px) {\n      width: 100%;\n      margin: 10px 0 0 0;\n    }\n\n    &:last-child {\n      margin-right: 0;\n    }\n\n    &:hover, &:focus {\n      background: darken($color: $blue, $amount: 5%);\n    }\n  }\n\n  &__popup-title {\n    color: $primary;\n    font-weight: 700;\n    font-size: 25px;\n    margin-bottom: 20px;\n\n    @media(max-width: 1199px) {\n      font-size: 20px;\n      margin-bottom: 15px;\n    }\n\n    @media(max-width: 575px) {\n      font-size: 16px;\n    }\n\n  }\n}\n","$primary: #484848;\n$secondary: #a2a1a1;\n$blue: #a993dc;\n$red: #ef726b;\n$green: #7dca3f;\n$brown: #d88861;\n$yellow: #ffad0d;\n$shadow: rgb(0, 0, 0, .10);\n$borderColor: #e6e6e6;\n\n@mixin placeholder($color) {\n  &::-moz-placeholder {\n    color: $color;\n    opacity: 1;\n  }\n  &::-webkit-input-placeholder {\n    color: $color;\n    opacity: 1;\n  }\n  &:-ms-input-placeholder {\n    color: $color;\n    opacity: 1;\n  }\n  &::-ms-input-placeholder {\n    color: $color;\n    opacity: 1;\n  }\n  &::placeholder {\n    color: $color;\n    opacity: 1;\n  }\n}"],"sourceRoot":""}]);
