t-weight: 500;\n  }\n\n  &__radio {\n    display: flex;\n    align-items: center;\n    position: relative;\n    cursor: pointer;\n    margin-bottom: 20px;\n\n    @media(max-width: 767px) {\n      align-items: flex-start;\n    }\n\n    &:last-child {\n      margin-bottom: 0;\n    }\n  }\n\n  &__radio-input {\n    position: absolute;\n    z-index: -1;\n    opacity: 0;\n\n    &:checked ~ {\n      #{$root} {\n        &__radio-el {\n          border-color: $blue;\n        }\n        &__radio-el::after {\n          opacity: 1;\n        }\n      }\n    }\n  }\n\n  &__radio-el {\n    width: 20px;\n    height: 20px;\n    border-radius: 10px;\n    border: 1px solid $secondary;\n    display: inline-flex;\n    align-items: center;\n    justify-content: center;\n    margin-right: 10px;\n    transition: all .2s ease;\n\n    @media(max-width: 767px) {\n      width: 16px;\n      height: 16px;\n      border-radius: 8px;\n      margin-right: 7px;\n      top: 4px;\n      position: relative;\n    }\n\n    &::after {\n      content: \"\";\n      width: 10px;\n      height: 10px;\n      border-radius: 5px;\n      background: $blue;\n      opacity: 0;\n\n      @media(max-width: 767px) {\n        width: 8px;\n        height: 8px;\n        border-radius: 4px;\n      }\n    }\n  }\n\n  &__radio-text {\n    font-size: 16px;\n    line-height: 21px;\n\n    @media(max-width: 767px) {\n      max-width: calc(100% - 90px);\n    }\n  }\n\n  &__radio-ico {\n    margin-left: 10px;\n    display: flex;\n    width: 60px;\n    height: auto;\n\n    @media(max-width: 767px) {\n      position: absolute;\n      top: 0;\n      right: 0;\n    }\n  }\n\n}\n","$primary: #484848;\n$secondary: #a2a1a1;\n$blue: #a993dc;\n$red: #ef726b;\n$green: #7dca3f;\n$brown: #d88861;\n$yellow: #ffad0d;\n$shadow: rgb(0, 0, 0, .10);\n$borderColor: #e6e6e6;\n\n@mixin placeholder($color) {\n  &::-moz-placeholder {\n    color: $color;\n    opacity: 1;\n  }\n  &::-webkit-input-placeholder {\n    color: $color;\n    opacity: 1;\n  }\n 