 overflow: hidden;\n    border-radius: 7px;\n\n    @media(max-width: 1440px) {\n      width: 50px;\n      margin-right: 10px;\n    }\n\n    &::after {\n      content: \"\";\n      display: block;\n      padding-top: 100%;\n    }\n  }\n\n  &__product-thumb {\n    top: 0;\n    left: 0;\n    position: absolute;\n    max-width: none;\n    width: 100%;\n    height: 100%;\n    object-fit: cover;\n    object-position: 50% 50%;\n  }\n\n  &__product-link {\n    top: 0;\n    left: 0;\n    position: absolute;\n    width: 100%;\n    height: 100%;\n    z-index: 1;\n  }\n\n  &__product-group {\n    width: 100%;\n    max-width: calc(100% - 80px);\n    @media(max-width: 1440px) {\n      max-width: calc(100% - 60px);\n    }\n  }\n\n  &__product-title {\n    font-weight: 500;\n    font-size: 15px;\n    color: $primary;\n    margin-bottom: 10px;\n    @media(max-width: 991px) {\n      font-size: 14px;\n    }\n  }\n\n  &__right {\n    width: 100%;\n    max-width: calc(100% - 300px);\n    display: flex;\n    align-items: center;\n\n    @media(max-width: 1740px) {\n      max-width: calc(100% - 250px);\n    }\n    @media(max-width: 1440px) {\n      max-width: calc(100% - 200px);\n    }\n    @media(max-width: 991px) {\n      max-width: calc(100% - 180px);\n    }\n  }\n\n  &__col {\n    width: 100%;\n    max-width: calc(33.333%);\n  }\n\n  &__price {\n    display: flex;\n    align-items: flex-end;\n  }\n\n  &__price-val {\n    font-size: 20px;\n    font-weight: 700;\n    color: $primary;\n    margin-right: 5px;\n    @media(max-width: 1740px) {\n      font-size: 18px;\n    }\n    @media(max-width: 991px) {\n      font-size: 16px;\n    }\n  }\n\n  &__price-currency {\n    font-size: 18px;\n    font-weight: 400;\n    @media(max-width: 1740px) {\n      font-size: 16px;\n    }\n    @media(max-width: 991px) {\n      font-size: 14px;\n    }\n  }\n\n  &__remove {\n    width: 60px;\n    height: 60px;\n    position: absolute;\n    top: calc(50% - 30px);\n    right: 30px;\n    border-radius: 10px;\n   