px) {\n      margin: 0 auto;\n    }\n  }\n\n  &__logo {\n    display: flex;\n    width: 100%;\n    max-width: 300px;\n    margin-right: 20px;\n\n    @media(max-width: 1199px) {\n      max-width: 200px;\n    }\n  }\n\n  &__content {\n    width: 100%;\n    max-width: 790px;\n    display: flex;\n    justify-content: space-between;\n    align-items: center;\n\n    @media(max-width: 1199px) {\n      max-width: 690px;\n    }\n\n    @media(max-width: 991px) {\n      max-width: calc(100% - 220px);\n    }\n\n    @media(max-width: 575px) {\n       max-width: 100%;\n    }\n  }\n\n  &__link-list {\n    display: flex;\n    flex-wrap: wrap;\n\n    @media(max-width: 991px) {\n      display: none;\n    }\n  }\n\n  &__link-item {\n    display: flex;\n    align-items: center;\n    padding: 13px 25px;\n    font-size: 16px;\n    font-weight: 600;\n    line-height: 1.2;\n\n    &--gift {\n      background: #fff6f7;\n      color: $red;\n\n      #{$root} {\n        &__link-ico {\n          fill: $red;\n        }\n      }\n    }\n\n    &--delivery {\n      background: #fff6f1;\n      color: $brown;\n      margin-left: 10px;\n\n      #{$root} {\n        &__link-ico {\n          fill: $brown;\n        }\n      }\n    }\n\n    @media(max-width: 1199px) {\n      font-size: 15px;\n    }\n  }\n\n  &__link-ico {\n    width: 20px;\n    height: 20px;\n    margin-right: 10px;\n\n    @media(max-width: 1199px) {\n      margin-right: 5px;\n    }\n  }\n\n  &__contacts {\n    padding: 0 0 0 35px;\n    width: 100%;\n    max-width: 240px;\n    margin: 6px 0 0 10px;\n    position: relative;\n    @media(max-width: 991px) {\n      margin-left: auto;\n      padding-left: 30px;\n    }\n    @media(max-width: 575px) {\n      margin: 15px 0 0;\n      padding: 0;\n      text-align: center;\n      max-width: 100%;\n    }\n  }\n\n  &__contacts-ico {\n    fill: $primary;\n    width: 20px;\n    height: 20px;\n    position: absolute;\n    top: 2px;\n    left: 0;\n    @media(max-width: 991px) {\n      width: 18px;\n      