e {\n    max-width: calc(100% - 80px);\n    width: 100%;\n  }\n\n  &__current-price {\n    font-size: 20px;\n    color: $red;\n    margin-right: 20px;\n\n    #{$root} {\n      &__price-label {\n        font-size: 18px;\n      }\n    }\n\n    @media(max-width: 575px) {\n      font-size: 18px;\n      margin-right: 15px;\n    }\n  }\n\n  &__price-val {\n    font-weight: bold;\n    margin-right: 5px;\n  }\n\n  &__old-price {\n    font-size: 16px;\n    color: $primary;\n    opacity: .2;\n    text-decoration: line-through;\n    #{$root} {\n      &__price-label {\n        font-size: 15px;\n\n        @media(max-width: 575px) {\n          font-size: 14px;\n        }\n      }\n    }\n\n    @media(max-width: 575px) {\n      font-size: 15px;\n    }\n  }\n}\n","$primary: #484848;\n$secondary: #a2a1a1;\n$blue: #a993dc;\n$red: #ef726b;\n$green: #7dca3f;\n$brown: #d88861;\n$yellow: #ffad0d;\n$shadow: rgb(0, 0, 0, .10);\n$borderColor: #e6e6e6;\n\n@mixin placeholder($color) {\n  &::-moz-placeholder {\n    color: $color;\n    opacity: 1;\n  }\n  &::-webkit-input-placeholder {\n    color: $color;\n    opacity: 1;\n  }\n  &:-ms-input-placeholder {\n    color: $color;\n    opacity: 1;\n  }\n  &::-ms-input-placeholder {\n    color: $color;\n    opacity: 1;\n  }\n  &::placeholder {\n    color: $color;\n    opacity: 1;\n  }\n}"],"sourceRoot":""}]);
